import document from 'global/document';
import customEvents from '~global/variables/custom-events.json';
import fireCustomEvent from '~global-js/helpers/custom-events';
import initI18n from '~components/i18n/i18next';
import axios from '~global-js/helpers/axios';
import { getLocale } from '~global-js/helpers/locale';

class GeoPhoneNumberComponent {
  constructor(props = {}) {
    this.countryCode = window.hurricane.clientInfo.country
      ? window.hurricane.clientInfo.country.toUpperCase() : null;
    this.continentCode = window.hurricane.clientInfo.region
      ? window.hurricane.clientInfo.region.toUpperCase() : null;

    this.props = props;
    this.result = null;
    this.localNumber = null;
  }

  init() {
    this.generateResult();
  }

  generateResult() {
    axios.get('/apis/v1/setting/phone-numbers').then((response) => {
      const { geo_phone_number, local_phone_number } = response.data;
      if (this.continentCode === 'NA' || this.continentCode === 'SA') {
        const regionalData = geo_phone_number.regional[this.continentCode];
        this.result = this.formatData(regionalData);
      } else {
        const countryData = geo_phone_number[this.countryCode];
        this.result = this.formatData(countryData);
      }

      const localeData = local_phone_number[getLocale()];
      const localNumbers = this.localNumberEvent(localeData);
      this.localNumber = localNumbers;
      if (!this.result) {
        this.result = localNumbers;
      }

      if (this.result !== null) {
        window.hurricane = window.hurricane || {};
        window.hurricane.ipBasedPhoneNumber = this.result;
        fireCustomEvent(customEvents.geoIpPhoneNumberInitialised.name, this.result);
      }
    }).catch((error) => {
      console.warn(error);
    });
  }

  formatData(data) {
    const phoneData = data;
    for (let i = 0; i < phoneData.length; i++) {
      phoneData[i] = {
        ...phoneData[i],
        title: this.props.i18n.t(phoneData[i].key),
        label: 'normal',
      };
    }
    return phoneData;
  }

  localNumberEvent(number) {
    let localNumber = number;
    localNumber = localNumber.map(language => ({
      ...language,
      number: language.value,
      title: this.props.i18n.t(language.type),
      label: 'normal',
    }));
    return localNumber;
  }

  localStorageEvent(name, list) {
    if (typeof Storage !== 'undefined') {
      const data = list;

      if (localStorage.getItem(name) && data && data[localStorage.getItem(name)]) {
        data[localStorage.getItem(name)].title =
          this.props.i18n.t(data[localStorage.getItem(name)].key);
        data[localStorage.getItem(name)].label = name;
        if (this.result) {
          this.result = this.result.concat(data[localStorage.getItem(name)]);
        } else {
          this.result = this.localNumber.concat(data[localStorage.getItem(name)]);
        }
      } else {
        const value = data.indexOf(data[Math.floor(Math.random() * data.length)]);
        localStorage.setItem(name, value);
        data[value].title = this.props.i18n.t(data[value].key);
        data[value].label = name;
        if (this.result) {
          this.result = this.result.concat(data[value]);
        } else {
          this.result = this.localNumber.concat(data[value]);
        }
      }
    }
  }
}

export default function initGeoPhoneNumber() {
  if (window.hurricane && window.hurricane.clientInfo) {
    initI18n().then(() => {
      const geoPhoneNumberComponent = new GeoPhoneNumberComponent({
        i18n: window.hurricane.i18next,
      });

      geoPhoneNumberComponent.init();
    });
  } else {
    document.addEventListener(customEvents.clientInfo.name, () => {
      initI18n().then(() => {
        const geoPhoneNumberComponent = new GeoPhoneNumberComponent({
          i18n: window.hurricane.i18next,
        });

        geoPhoneNumberComponent.init();
      });
    });
  }
}



