import i18next from 'i18next';
import XHR from 'i18next-xhr-backend';
import axios from '~global-js/helpers/axios';
import { Promise } from 'rsvp';
import getCdnBasePath from '~global/js/helpers/cdn';
import baseConfig, { pluralProcessor } from '~components/i18n/base-config';
import window from 'global/window';
import fireCustomEvent from '~global-js/helpers/custom-events';
import events from '~global/variables/custom-events.json';

export const buildConfig = () => ({
  ...baseConfig,
  backend: {
    loadPath: `${getCdnBasePath()}translations/%lng%/%ns%.%lng%.json`,
    ajax: (url, options, callback) => {
      const axiosInstance = axios.create();
      // Remove exist header such as x-call-id to avoid cross origin issue
      axiosInstance.defaults.headers.common = {};
      axiosInstance.get(url).then((response) => {
        callback(JSON.stringify(response.data), response);
      }).catch((e) => {
        callback(null, {
          status: 404,
        });
        throw (e);
      });
    },
  },
});

/**
 * ## For react-i18next-provider (larger apps)
 *
 * Initialise i18n module, which fetches translations dynamically from the server
 * (via json files). This function is intended for usage with react-i18next provider.
 *
 * @param {Object} configOverrides
 * @returns {Class} Initialised i18next class object
 *
 * Signature: `initI18n({ lng: 'en-GB' })`
 */
export function initI18nClass(configOverrides, callback = () => null) {
  let i18nextConfig = buildConfig();

  if (configOverrides) {
    i18nextConfig = { ...buildConfig(), ...configOverrides };
  }

  return i18next.use(XHR).use(pluralProcessor).init(i18nextConfig, callback);
}

export function init(configOverrides) {
  if (window.hurricane && window.hurricane.staticHash) {
    initI18nModule(configOverrides).then(setI18next);
  } else {
    document.addEventListener(events.staticHashInitialised.name, () => {
      initI18nModule(configOverrides).then(setI18next);
    });
  }
}

function setI18next() {
  window.hurricane = window.hurricane || {};
  window.hurricane.i18nInitialised = true;
  window.hurricane.i18next = i18next;
  fireCustomEvent(events.i18nInitialised.name);
}

/**
 * ## Default export (for most microapps)
 *
 * Initialise i18n module, which fetches translations dynamically from the server
 * (via json files).
 *
 * @param {Object} configOverrides
 * @returns {Promise} Resolves with `trans`.
 *
 * Signature: `initI18n({ lng: 'en-GB' }).then((trans) => { trans(key) })`
 */
export function initI18nModule(configOverrides = {}) {
  if (window.hurricane && window.hurricane.staticHash && window.hurricane.staticHash.trans) {
    Object.assign(configOverrides, {
      backend: {
        loadPath: `${getCdnBasePath()}translations/%lng%/%ns%.%lng%.${window.hurricane.staticHash.trans}.json`,
      },
    });
  }

  return new Promise((resolve, reject) => {
    initI18nClass(configOverrides, (error, trans) => {
      if (error) {
        reject('failed to load translations');
      }
      resolve(trans);
    });
  });
}

export default function initI18n() {
  return new Promise((resolve) => {
    if (window.hurricane && window.hurricane.i18nInitialised) {
      resolve();
    } else {
      document.addEventListener(events.i18nInitialised.name, () => {
        resolve();
      });
    }
  });
}



